.profile {
  display: flex;
  align-items: center;
  position: relative;
  padding: 0.25rem 0;
}

.profile__image {
  width: 2rem;
  aspect-ratio: 1/1;
  border-radius: 50%;
}

.profiledropdown {
  position: absolute;
  top: 100%;
  right: 0;
  background-color: var(--clr-primary-dark);
  padding: 0.25rem 0.75rem;
  display: none;
  z-index: 9999;
  padding: 0 0.5em 0.5em;
}

.profile:focus-within .profiledropdown,
.profile:hover .profiledropdown {
  display: block;
}

.profiledropdown a {
  display: block;
  margin-block: 0.5em;
  color: var(--clr-accent);
}

.profiledropdown p {
  display: block;
  white-space: nowrap;
  margin-block: 0.5em;
}

.navbar--desktop {
  background-color: var(--clr-primary);
  width: 100%;
  display: grid;
  grid-template-columns: 2em 1fr 3fr 1fr 2em;
  justify-content: center;
  align-items: center;
}

.navbar__logo--desktop {
  font-size: 1.75em;
  max-height: 2em;
  justify-self: start;
  grid-column: 2;
  margin: 0.25rem 0;
}

.navbar__list--deskop {
  justify-self: center;
  grid-column: 3;
  display: flex;
  list-style: none;
  grid-gap: 1em;
  gap: 1em;
  padding: 0;
  margin: 0;
}

.navbar__login--desktop {
  justify-self: end;
  grid-column: -3;
}

:root {
  --clr-primary-light: #a3d4d4;
  --clr-primary: #2875a5;
  --clr-primary-dark: #05314c;
  --clr-secondary: #333;
  --clr-secondary-light: white;
  --clr-accent: #b92e2a;
  --clr-accent-dark: hsl(2, 66%, 35%);
}

body {
  /* Tar bort förinställd rymd i body, då jag hellre vill ändra det själv */
  margin: 0;
  padding: 0;
  background-color: #333;
  background-color: var(--clr-secondary);
  color: white;
  color: var(--clr-secondary-light);
  font-family: "Poppins", sans-serif;
}

*,
*::before,
*::after {
  /* Inkluderar padding och border i width och height utrökningar. */
  box-sizing: border-box;
}

img {
  /* Gör så att bilder inte växer sig större än sin förälder. */
  max-width: 100%;
}

main {
  margin-inline: 2em;
}

